import React from 'react'

const ItemInCart = (props) => {
  

  const setObjectToDelete = () => {
    props.deleteItemFromCart(props.id)
  }



    return (
        <div>
        <div className='items'>
          <div className='cartItem d-flex align-center mb-20'>
            <img className='sneakers_img mr-15' src={ props.url } />
            <div className='mr-20'>
              <p className='mb-5'>{ props.name }</p>
              <b>{ props.price }</b>
            </div>
            <button onClick = {setObjectToDelete}><img  className='actions_icon remove' src='https://image.flaticon.com/icons/png/512/748/748122.png' /></button>
          </div>
          
          </div>
            
        </div>
    )
}

export default ItemInCart;
