import React, { useEffect } from 'react'

const Favourite = (props) => {

    const deleteItem = () => {
        props.deleteItemFromFav(props.id)
    }


   return (
        <div>
            
            <h1>{props.name}</h1> 
            <button onClick={deleteItem}>Delete</button>
            <div><img src = {props.url}/></div>
            <h3>{props.price}</h3>
            
            </div>
    )
}

export default Favourite
