import React, { useState } from 'react'
import ItemInCart from './ItemInCart'

const Cart = (props) => {

  let itemsInBag = props.cartItems.map((el) => {
    return (<ItemInCart id = {el.id} deleteItemFromCart = {props.deleteItemFromCart} name={el.name} url={el.url} price={el.price} />)
  })

 





  let cartVisionToFalse = () => {
    props.setCartOpened(false)
  }

    return (
        <div>
          <div className='shadow'>
          <div className='drawerBlock'>
          <h2 className='mb-30 d-flex justify-between '>Cart <button className='cu-p'><img onClick={cartVisionToFalse} className='actions_icon remove' src='https://image.flaticon.com/icons/png/512/748/748122.png' /></button></h2 >

         {itemsInBag}


         <div className='totalBlock'>
         <ul>
           <li className='d-flex'>
             <span>Total: </span>
             <div></div>
             <b>{props.total} $</b>
           </li>
          <li className='d-flex'>
          <span>With tax: 5%</span>
          <div></div>
          <b>{props.totalWithFax} $</b>
          </li>
         </ul>
         <button>Continue</button>

         </div>

        </div>
        </div>
            
            
        </div>
    )
}

export default Cart
