import React, { useEffect } from 'react'
import { useState } from 'react'
import { dataAPI } from '../API';
import MyLoader from './MyLoader';

const Item = ({name, url, price, addToBag, addToFavourite, addToUnliked}) => {

  let [checked, setChecked] = useState(false);

  let [favourite, setFavourite] = useState(false)

  let [isDislike, setDislake] = useState(false)

  let [disableButton, setDisableButton] = useState(false)

  const [isLoading, setIsLoading] = useState(false)

  let setItemChecked = () => {
    dataAPI.setCartData(name, url, price);
    setChecked(true)
    addToBag({name, url, price})
  }
  

  let setItemChecke = () => {
    setChecked(false)
  }

  const setToFavourite = () => {
    setFavourite(true);
    dataAPI.setFavouriteSneakers( name, url, price)
    addToFavourite( {name, url, price} )
    setDisableButton(true);
 
  }

  const toDislake = () => {
    setDislake(true)

    dataAPI.setUnlikedItems(name, url, price)
    addToUnliked({name, url, price})
  }

  const toCancelDislake = () => {
    setDislake(false)
  }


    return (
        <div>
        <div className='card'>
          <div className='favourite'>
            {disableButton ?  <button onClick={setToFavourite} disabled>{favourite ? <img className='actions_icon' src='https://image.flaticon.com/icons/png/512/1077/1077086.png' /> : <img className='actions_icon' src='https://image.flaticon.com/icons/png/512/1077/1077035.png' />}</button> : 
             <button onClick={setToFavourite}>{favourite ? <img className='actions_icon' src='https://image.flaticon.com/icons/png/512/1077/1077086.png' /> : <img className='actions_icon' src='https://image.flaticon.com/icons/png/512/1077/1077035.png' />}</button>}
          </div>
          <img className='sneakers_img' src={url}/>
          <p className='title'>{name}</p>
          <div className='d-flex justify-between'>
            <div className='d-flex flex-column align-center'>
              <p>Price: </p>
              <b>{price} $</b>
            </div>
            <button onClick={setItemChecked} >{checked ? <img className='actions_icon' src='https://image.flaticon.com/icons/png/512/51/51281.png' /> : <img className='header_icon' src='https://image.flaticon.com/icons/png/512/992/992651.png' />}</button>
           
            {checked ? <button onClick={setItemChecke}><img className='actions_icon' src='https://image.flaticon.com/icons/png/512/1828/1828774.png' /></button> : ''}
            {isDislike ? <button onClick = {toCancelDislake}><img className='actions_icon' src=' https://image.flaticon.com/icons/png/512/153/153739.png'/></button> : <button onClick = {toDislake}><img className='actions_icon' src='https://image.flaticon.com/icons/png/512/152/152516.png'/></button>}
         
          </div>
        </div>
        </div>
    
    ) }

   
export default Item
