import React from 'react'

const Unliked = (props) => {

    const deleteitems = () => {
        props.deleteUnlikedItems(props.id)
    }


    return (
        <div>
            <h1>{props.name}</h1>
            <button onClick={deleteitems}>Delete</button>
            <h1><img src={props.url} /></h1>
            <h1>{props.price}</h1>
            
        </div>
    )
}

export default Unliked
