import React from "react"
import ContentLoader from "react-content-loader"

const MyLoader = (props) => (
  <ContentLoader 
    speed={2}
    width={400}
    height={460}
    viewBox="0 0 400 460"
    backgroundColor="#f3f3f3"
    foregroundColor="#ecebeb"
    {...props}
  >
    <rect x="18" y="28" rx="10" ry="10" width="218" height="88" /> 
    <rect x="21" y="135" rx="10" ry="10" width="218" height="15" /> 
    <rect x="85" y="183" rx="0" ry="0" width="9" height="0" /> 
    <rect x="22" y="167" rx="10" ry="10" width="149" height="20" /> 
    <rect x="31" y="227" rx="10" ry="10" width="74" height="36" /> 
    <rect x="183" y="211" rx="10" ry="10" width="43" height="54" />
  </ContentLoader>
)

export default MyLoader