import React from 'react'
import { NavLink } from 'react-router-dom';

const Header = (props) => {


    return (
        <div>
        <header className='d-flex justify-between align-center'>
        <div className='align-center d-flex'>
       <NavLink to='/main'><img className='header_img' src='https://upload.wikimedia.org/wikipedia/commons/thumb/a/a7/React-icon.svg/1280px-React-icon.svg.png' /></NavLink>
        <div  className='headerInfo'>
          <h3 className='text-uppercase'>Sneakers Store</h3>
          <p className='opacity-5'>store</p>
        </div>
        </div>
        
        <ul className='d-flex'>
        <li className='mr-30'><img onClick={props.cartVisionToTrue} className='header_icon cu-p' src='https://image.flaticon.com/icons/png/512/833/833314.png'/><span>{props.totalWithFax} $</span></li>
        <li className='mr-30'><img className='header_icon cu-p' src='https://image.flaticon.com/icons/png/512/747/747376.png' /></li>
  
        </ul>
      </header>
            
        </div>
    )
}

export default Header
