import React, { createContext, useContext, useEffect, useState } from 'react'
import './index.css';
import Cart from './Components/Cart.jsx';
import Item from './Components/Item';
import Header from './Components/Header';
import { dataAPI } from './API';
import { Route } from 'react-router-dom';
import Favourite from './Components/Favourite';
import { NavLink } from 'react-router-dom';
import Unliked from './Components/Unliked';
import MyLoader from './Components/MyLoader';



const App = () => {

  let [isCartOpened, setCartOpened] = useState(false);

  const [locState, setLocState] = useState([]);

  const [cartItems, setCartItems] = useState([]);

  const [favouriteItems, setFavouriteItems] = useState([]);

  const [unliked, setUnliked] = useState([]);

  const [inputValue, setInteval] = useState('');

  const [loader, setLoader] = useState(false)
  console.log(loader)

  let total = cartItems.reduce((sum, obj) =>{
    return (Number(obj.price) + sum)
  }, 0) // cart items total sum

   let totalWithFax = (total + (total/100) * 5)

  


  useEffect(() => {
    setLoader(true)
    dataAPI.getSneakersData().then((response) => {
      setLocState(response.data)
      setLoader(false)
    });

    dataAPI.getCartData().then((response) =>  { setCartItems(response.data) } );

    dataAPI.getFavouriteSneakers().then((response) =>  {setFavouriteItems(response.data) })
   

    dataAPI.getUnlikedItems().then(response => setUnliked(response.data))
    
  }, [])

  const cartVisionToTrue = () => {
    setCartOpened(true)
  }

  const onAddToBag = (el) => {
    setCartItems([...cartItems, el])
  }


  const addToFavourite = (obj) => {
    setFavouriteItems( [...favouriteItems, obj])
  }

  const addToUnliked = (newObj) => {
    setUnliked([...unliked, newObj])
  }

  const deleteItemFromCart = (obj) => {
    dataAPI.deleteDataFromCart(obj)
  }

  const deleteItemFromFav = (id) => {
    dataAPI.deleteItemFromFavourites(id);
  }

  const deleteUnlikedItems = (id) => {
    dataAPI.deleteUnlikedItems(id);
  }


 let itemElements = locState.filter(item => item.name.toLowerCase().includes(inputValue.toLowerCase())).map((el) => { //search realized
  return (<Item id = {el.id} name={el.name} url={el.url} price={el.price}  addToBag={onAddToBag} addToFavourite={addToFavourite} addToUnliked = {addToUnliked}
    cartItems = {cartItems}  />) })

let loadingElements = locState.map((el) => { 
      return (<MyLoader/>) })

  let favouriteElements = favouriteItems.map((el) => {
    return (<Favourite id = {el.id} name={el.name} url={el.url} price={el.price} deleteItemFromFav = {deleteItemFromFav} />)
  })

  let unlikedElements = unliked.map((el) => {
    return(<Unliked id = {el.id} name = {el.name} url = {el.url} price = {el.price} deleteUnlikedItems={deleteUnlikedItems}/>)

  })

  let getCurrentValue = (el) => {
    setInteval(el.currentTarget.value)
  }

  let cancelInput = () => {
    setInteval('')
  }

  return (
    
    <div className='container clear'>
      <NavLink to='/favourite'><button><img className='actions_icon' src='https://image.flaticon.com/icons/png/512/54/54381.png' /></button></NavLink>
      <NavLink to='/unliked'><button><img className='actions_icon' src='https://image.flaticon.com/icons/png/512/637/637595.png' /></button></NavLink>

        {isCartOpened ? <Cart deleteItemFromCart = {deleteItemFromCart} setCartOpened = {setCartOpened} cartItems = {cartItems} total = {total} totalWithFax = {totalWithFax} /> : ''}
      <Header cartVisionToTrue = {cartVisionToTrue} totalWithFax = {totalWithFax}/>


      <Route path="/main" render={() => <div className='content p-40'>
        

        <div  className='d-flex align-center justify-between mb-40'>
        <h1 className=''>{inputValue ? 'Searching by a sneakers...': 'All sneakers'}</h1>
        <div className='search d-flex'>
          <img className='actions_icon' src = 'https://image.flaticon.com/icons/png/512/622/622669.png' />
          <input onChange={getCurrentValue} value={inputValue} placeholder='Search...'/>
          {inputValue ? <button onClick={cancelInput}><img className='actions_icon' src='https://image.flaticon.com/icons/png/512/1828/1828774.png' /></button> : ''}
        </div>
        </div>
        <div className='d-flex itemsContainer flex-wrap'>
          {loader ? loadingElements : itemElements}

        </div>   
      </div> }/>

      {unliked ?  <Route path="/unliked" render={() => unlikedElements}/>: ''}


      {favouriteItems ?  <Route path="/favourite" render={() => favouriteElements}/> : window.alert('-')}

      
     
    </div>
  )
}

export default App

