import axios from 'axios'


export const dataAPI = {
    getSneakersData() {
        return axios.get('https://610569584b92a000171c5edf.mockapi.io/sneakers');
    },

    setCartData(name, url, price) {
        return axios.post('https://610569584b92a000171c5edf.mockapi.io/cart', {name, url, price})
    },

    getCartData() {
        return (axios.get('https://610569584b92a000171c5edf.mockapi.io/cart'))
    },

    deleteDataFromCart(id) {
        return axios.delete(`https://610569584b92a000171c5edf.mockapi.io/cart/${id}`)
    },

    setFavouriteSneakers(name, url, price, id) {
     return axios.post('https://610569584b92a000171c5edf.mockapi.io/favourite', {name, url, price})
     
    },

    getFavouriteSneakers() {
       return axios.get('https://610569584b92a000171c5edf.mockapi.io/favourite');
    },

    deleteItemFromFavourites(id) {
        return (axios.delete(`https://610569584b92a000171c5edf.mockapi.io/favourite/${id}`))
    },

    setUnlikedItems (name, url, price) {
        return axios.post('https://610569584b92a000171c5edf.mockapi.io/unfavourite', {name, url, price})
    },

    getUnlikedItems() {
        return axios.get('https://610569584b92a000171c5edf.mockapi.io/unfavourite');
    },

    deleteUnlikedItems(id) {
        debugger;
        return axios.delete(`https://610569584b92a000171c5edf.mockapi.io/unfavourite/${id}`);
        
    }
}